.location{
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1000;
position: absolute;
padding: 26px;
top: 50%;
left: 50%;
width: 280px;
transform: translate(-50%, -50%);
background-color: var(--primary-text-color);
border: 2px solid #f8d24e;
border-radius: 10px;
@media screen and (min-width: 768px) {
padding: 32px 60px;
width: 568px;
}
}
.modal-location-close-btn {
    width: 16px;
    height: 16px;
    position: absolute;
    top: 8px;
    right: 8px;
    padding: 0;
    border: none;
    background-color: var(--primary-text-color);
  
    @media screen and (min-width: 768px) {
      top: 20px;
      right: 20px;
    }
  }
  .modal-location-close-icon{
    width: 16px;
    height: 16px;
    @media screen and (min-width: 768px) {
  width: 20px;
  height: 20px;
    }
  }
.modal-location__text {
    display: block;
    text-align: center;
    margin-bottom: 20px;
  
    font-family: var(--secondary-font-family);
    color: var(--secondary-text-color);
    font-size: 16px;
    line-height: 1.5;
  
    @media screen and (min-width: 768px) {
      margin-bottom: 32px;
      font-size: 20px;
      line-height: 1,5;
    }
    @media screen and (min-width: 1200px){
    font-size: 20px;
       }
  }
  
  .modal_location-map {
    display: block;
    width:224px;
    height:200px;
    margin-bottom: 24px;
    border-radius: 10px;

    @media screen and (min-width: 768px)
    {
      width: 448px;
        height: 260px;
    }}
.modal-location__link {
    align-items: center;
    padding: 12px 34px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    border-radius: 10px;
    background: var(--accent-color);
    color: var(--icon-modal-close);
    transition-property: color, background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    &:hover
    {
        background-color: #0A8817;
    }
    &:focus
    {
        color: var(--accent-color);
        background-color:#0A8817;;
        outline: none;
    }
}
