.products {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 60px;
  padding-bottom: 60px;
  background-image: url('../images/desktop/product/bcg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #f5f5f5;

  // @media screen and (min-device-pixel-ratio: 2),
  //         screen and (min-resolution: 192dpi),
  //         screen and (min-resolution: 2dppx) {
  //         background-image: linear-gradient url('../images/desktop/product/bcg@2x.png');
  //         }

  @media screen and (min-width: 768px) {
    padding-top: 61px;
    padding-bottom: 82px;
  }
  @media screen and (min-width: 1200px) {
    padding-top: 61px;
    padding-bottom: 66px;
  }
}

.products__par-title {
  font-family: var(--primary-font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-bottom: 12px;

  @media screen and (min-width: 768px) {
    font-size: 22px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
  }
}

.products__title {
  font-family: var(--secondary-font-family);
  font-weight: 400;
  font-size: 30px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-bottom: 150px;

  @media screen and (min-width: 768px) {
    margin-bottom: 200px;
    font-size: 34px;
    line-height: 43px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 36px;
    line-height: 45px;
    letter-spacing: 0.06em;
  }
}

.products__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 164px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    gap: 16px;
    justify-content: space-between;
  }
}
.products__card {
  position: relative;
  width: 100%;
  min-height: 420px;
  padding-top: 130px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 50px;
  text-align: center;

  &:hover {
    box-shadow: 5px 5px 8px 2px rgba(0, 0, 0, 0.3);
  }

  @media screen and (min-width: 480px) {
    width: 440px;
  }
  @media screen and (min-width: 768px) {
    width: calc((100% - 16px * 2) / 3);
    min-height: 428px;
    padding-top: 120px;
    padding-left: 25px;
    padding-right: 25px;
  }
  @media screen and (min-width: 1200px) {
    width: calc((100% - 32px * 2) / 3);
    min-height: 535px;
    padding-top: 180px;
    padding-left: 65px;
    padding-right: 65px;
  }
}
.products__card-title {
  color: var(--secondary-text-color);
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;

  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 27px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 65px;
  }
}

.products__parag {
  font-weight: 400;
  font-size: 14px;
  line-height: 176%;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--secondary-text-color);

  @media screen and (min-width: 768px) {
    width: 172px;
  }

  @media screen and (min-width: 1200px) {
    width: 242px;
    height: 142px;
    font-size: 16px;
  }
}

.products__card-green {
  background: var(--img-ice-cream-bcg-color);
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.25);
  border-radius: 24px;
}

.products__card-green::before {
  content: '';
  position: absolute;
  width: 200.33px;
  height: 199.68px;
  top: 0px;
  left: 50%;
  z-index: 1;
  border-radius: 50%;
  background-position: 50%;
  background-image: url('../images/mobile/product/product1.png');
  background-repeat: no-repeat;
  background-size: contain;
  box-shadow: 0 12px rgb(205 225 197 / 0.7),
    0px 5.40397px 20.2649px rgba(0, 0, 0, 0.25);

  transform: translate(-50%, -50%);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: linear-gradient
      url('../images/mobile/product/product1@2x.png');
  }
  @media screen and (min-width: 768px) {
    width: 199.34px;
    height: 196.85px;
    background-image: url('../images/tablet/product/product1.png');
  }

  //  @media screen and (min-device-pixel-ratio: 2),
  //                 screen and (min-resolution: 192dpi),
  //                 screen and (min-resolution: 2dppx) {
  //                     background-image: linear-gradient url('../images/tablet/product/product1@2x.png');
  //                 }
  //         }

  @media screen and (min-width: 1200px) {
    width: 301.76px;
    height: 300.29px;
    background-image: url('../images/desktop/product/product1.png');
  }
}

