.modal-franchise {
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    background-color: var(--secondary-bcg-color);
    border: 2px solid var(--accent-color);
  width: 280px;
    border-radius: 10px;
    padding: 28px;
    position: absolute;
    overflow: auto;
    top: 60%;
    left: 50%;
    transform: translate(-50%,-50%)scale(1) rotate(0);
  
    @media screen and (min-width: 768px) {
  width: 568px;
    top: 55%;
    padding: 32px 60px;
  }
  
  }
  .backdrop {
    overflow-y: scroll;
  }
  
  .modal-franchise-close-btn  {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  top: 8px;
  right: 8px;
  padding: 0;
  border: none;
  color: var(--icon-modal-close);
    background-color: var(--background-color);
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1), fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      fill: var(--accent-color);
    }
  }
  // .backdrop.is-hidden {
  //   transform: scale(0) rotate(-220deg);
  //   opacity: 0;
  //   pointer-events: none;
  //   visibility: hidden;
  // }
  
  .modal-title {
    margin-left: auto;
    font-family: var(--secondary-font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    color: var(--secondary-text-color);
    margin-bottom: 20px;
  
    @media screen and (min-width: 768px) {
        font-size: 20px;
        line-height: 0,4;
    }
    @media screen and (min-width: 1200px) {
    font-size: 20px;
    line-height: 0,4;
  }
  }
  .modal-text-main{
    font-family: var(--primary-font-family);
    color: var(--secondary-text-color);
    font-weight: 400;
    text-align: left;
    margin-bottom: 32px;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;
    line-height: 1.4;
  
    @media screen and (min-width: 768px) {
        font-size: 16px;}
  }
    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
  
      }
  
  .modal-input {
    width: 100%;
    margin-bottom: 16px;
    height: 32px;
    border: 1px solid var(--primary-bcg-color);
    border-radius: 10px;
    padding-left: 30px;
    color: var(--secondary-text-color);
    font-weight: 400;
    font-size: 14px;
    outline: transparent;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
      width: 368px;
    }
    &:focus {
      border-color: var(--accent-color);
    }
    &:not(:focus):not(:placeholder-shown):valid {
      background-color: rgba(72, 146, 67, 0.361);
    }
    &:not(:focus):not(:placeholder-shown):invalid {
      background-color: rgba(202, 87, 106, 0.361);
    }
    &::placeholder {
  
      font-weight: 400;
      font-size: 12px;
      line-height: 1.16;
      letter-spacing: 0.01em;
      color:var(--icon-modal-close);
    }
  }
  
  .modal-text {
    // display: block;
    width: 100%;
    height: 64px;
    border: 1px solid var(--primary-bcg-color);
    border-radius: 10px;
    resize: none;
    padding: 12px;
    outline: transparent;
    margin-bottom: 20px;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:focus {
      border-color: var(--accent-color);
    }
    & ::placeholder {
      font-weight: 400;
      font-size: 12px;
      line-height: 1.16;
      letter-spacing: 0.01em;
      color: var(--icon-modal-close);
    }
  
    @media screen and (min-width: 768px) {
      width: 368px;
      height: 84px;
    }
  }
  
  .modal-send {
    outline: none;
    border: none;
    display: block;
    text-align: center;
    align-items: center;
    padding: 12px 34px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    border-radius: 10px;
    background: var(--accent-color);
    color: var(--icon-modal-close);
    transition-property: color, background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    &:hover
    {
        background-color: #0A8817;
    }
    &:focus
    {
        color: var(--accent-color);
        background-color:#0A8817;;
        outline: none;
    }
  }
  